@tailwind base;

@tailwind components;

/* Common */
/* ------------------------------------------------------------------------- */
.icon-btn {
  @apply p-1 rounded;
}

.icon-btn:hover {
  @apply bg-gray-200;
}

.icon-btn:focus {
  @apply outline-none bg-gray-200;
}

.btn {
  @apply text-white bg-green-500 border-b-2 border-green-600 h-8 py-1 px-4 rounded-sm flex justify-center items-center;
}

.btn:hover {
  @apply bg-green-600 border-green-700;
}

.btn:focus {
  @apply outline-none bg-green-700 border-green-700;
}

/* Navigation */
/* ------------------------------------------------------------------------- */
.nav-bar {
  @apply w-full flex fixed bottom-0 overflow-hidden justify-around border-t bg-white;
}

.nav-btn {
  @apply w-full h-16 text-sm flex flex-col justify-center items-center;
}

.nav-btn:hover {
  @apply bg-gray-200;
}

.nav-btn:focus {
  @apply outline-none bg-gray-200;
}

.mobile-header {
  @apply px-5 py-3 flex justify-between items-center bg-white flex-shrink-0;
}

/* Features */
/* ------------------------------------------------------------------------- */
.feature-header {
  @apply flex items-center px-5 py-3 border border-l-0 border-r-0 shadow-sm bg-white;
}

/* Notes */
/* ------------------------------------------------------------------------- */
.note {
  @apply bg-yellow-200 border-t-4 border-yellow-300 rounded-sm p-2 pb-0 mb-4 h-32 overflow-hidden;
  border-left: solid 1px #faf089;
  border-right: solid 1px #faf089;
  border-bottom: solid 1px #faf089;
}

.note:first-of-type {
  @apply mt-4;
}

@tailwind utilities;
